.slick-dots {
	font-size: 0;
	li {
		display: inline-block;
		vertical-align: top;
		margin: 0 8px;
		width: 14px;
		height: 14px;
		background: $blue;
		border-radius: 50%;
		cursor: pointer;
		@include trans;
		&.slick-active {
			background: $orange4; }
		&:hover {
			background: $orange4; }
		button {
			display: none; } } }

.section-advant {
	position: relative;
	padding-bottom: 27px;
	border-bottom: 1px solid $blue;
	&__global-img {
		position: absolute;
		right: -124px;
		bottom: -90px; } }
.advant-list {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap; }
.advant-block {
	display: inline-block;
	vertical-align: top;
	width: 364px;
	flex-basis: 364px;
	text-align: center;
	margin-bottom: 38px;
	&__img {
		height: 87px;
		margin-bottom: 25px;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		img {
			max-height: 87px;
			width: auto;
			max-width: 102px; } }
	&__title {
		margin-bottom: 25px;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			bottom: -12px;
			width: 30px;
			height: 2px;
			background-color: $orange;
			left: 50%;
			transform: translateX(-50%); } } }
.section-tabs {
	background: $blue-bg2;
	border-bottom: 1px solid $blue;
	padding-bottom: 55px;
	&__title {} }
.section-exp-slider {
	padding-bottom: 64px;
	border-bottom: 1px solid $blue; }
.why-we {
	@include cover;
	background-color: $orange-bg;
	padding-bottom: 34px;
	border-bottom: 1px solid $blue;
	&__inner {
		position: relative;
		padding-left: 360px; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 27px;
		.why-we-block {
			&:nth-of-type(1n) {
				padding-right: 38px; }
			&:nth-of-type(2n) {
				padding-left: 30px; } } }
	&__title {
		margin-bottom: 45px;
		@include m {
			margin-bottom: 20px; } }
	&__img {
		position: absolute;
		left: -106px;
		bottom: -51px;
		img {
			width: 427px;
			height: auto;
			max-height: 510px; } }
	&__bottom-text {
		text-align: center; } }
.why-we-block {
	width: 50%;
	flex-basis: 50%;
	margin-bottom: 20px;
	&__title {
		margin-bottom: 12px; } }
.reviews {
	// padding-bottom: 31px
	margin-bottom: 50px;
	// border-bottom: 1px solid $blue
	&__title {
		margin-bottom: 90px;
		// font-weight: 400
		@include m {
			margin-bottom: 45px; } }
	&__list {
		font-size: 0;
		.review {
			margin-right: 2.75%;
			&:last-of-type {
				margin-right: 0; } } } }
.review {
	width: 31.5%;

	background-color: $white;
	border: 2px solid $orange5;
	display: inline-block;
	vertical-align: top;
	padding: 9px 18px 9px;
	&__name {
		color: $black;
		font-size: 22px;
		font-weight: 400;
		margin-bottom: 11px;
		border-bottom: 1px solid $blue;
		padding-bottom: 4px; }
	&__video {
		// height: 0
		position: relative;
		border-bottom: 1px solid $blue;
		// height: 233px
		margin-bottom: 12px;
		height: 0;
		padding-bottom: 56.25%;
		// &:before
		// 	content: ''
		// 	position: absolute
		// 	left: 0
		// 	top: 0
		// 	height: 0
		// 	padding-bottom: 56.25%
		// 	width: 100%
		// 	z-index: -1
		iframe {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0; } }
	&__stars {
		text-align: center;
		svg {
			width: 20px;
			height: 20px;
			display: inline-block;
			vertical-align: top;
			fill: $orange5;
			margin: 0 3px;
			font-size: 20px; } } }
.care-section {
	background-color: $orange-bg2;
	border-top: 1px solid $blue;
	border-bottom: 1px solid $blue;

	padding-top: 52px;
	margin-top: 131px;
	margin-bottom: 80px;
	&__inner {
 }		// position: relative
	&__left {
		padding-left: 89px;
		max-width: 668px;
		text-align: center; }
	&__title {
		margin-bottom: 23px; }
	&__text {
		margin-bottom: 43px;
		p {
			font-weight: 400; } }
	&__form {
		font-size: 0;
		input {
			width: 263px;
			height: 62px;
			background-color: $white;
			border-radius: 2px;
			margin-right: 27px;
			display: inline-block;
			vertical-align: top;
			font-size: 18px;
			line-height: 62px;
			padding: 0 14px;
			@include rob; } }
	&__btn {
		display: inline-block;
		vertical-align: top;
		padding: 5px;
		background: $white;
		box-shadow: 0 0 5px rgba(187, 186, 186, 0.75);
		.btn {
			margin: 0; } }
	&__img {
		position: absolute;
		// top: -149px
		bottom: -78px;
		right: -60px;
		max-width: 435px;
		height: auto;
		max-height: 452px; }
	.l {
		padding-bottom: 48px; } }
.counter-block {
	padding-top: 1px;
	margin-bottom: 40px;
	&__inner {
		display: flex;
		justify-content: space-between; } }
.counter {
	flex-basis: 225px;
	text-align: center;
	&__text {
		font-size: 20px;
		margin-bottom: 30px;
		strong {
			font-weight: 700; } }
	&__numbers {}
	&__count {
		display: inline-block;
		vertical-align: top;
		margin: 0 5px;
		width: 52px;
		height: 78px;
		background-color: $white;
		border: 1px solid $blue;
		border-radius: 4px;
		text-align: center;
		font-weight: 700;
		font-size: 48px;
		padding-top: 10px;
		position: relative;
		z-index: 1;
		span {
			position: relative;
			z-index: 1; }
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: #afafaf;
			z-index: -1; } } }
.superman-block {
	background-color: $blue-bg;
	padding-top: 25px;
	padding-bottom: 20px;
	margin-top: 62px;
	height: 127px;
	margin-bottom: 39px;
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }
	&__big-text {
		color: $white;
		font-size: 30px;
		font-weight: 700;
		flex-shrink: 0;
		flex-basis: 294px; }
	&__text {
		font-size: 18px;
		color: $white;
		padding-left: 17px;
		padding-top: 2px; }
	&__img {
		width: 254px;
		height: auto;
		max-height: 155px;
		position: relative;
		top: -42px; }
	&__btn {
		padding: 4px;
		background: $white;
		.btn {
			margin: 0; } } }
.map {
	&__inner {
		height: 418px; } }
.pre-footer {
	padding-bottom: 25px;
	padding-top: 35px;
	&__inner {
		@include clr; }
	&__form {
		padding-left: 68px;
		width: 490px;
		float: left;
		text-align: center; }
	&__text {
		width: calc(100% - 490px);
		text-align: center;
		float: right;
		padding-top: 46px;
		@include lgmin {
			padding-left: 86px; }
		h2 {
			color: $blue-l;
			font-size: 60px;
			font-weight: 700;
			margin-bottom: 34px; }
		p {
			font-size: 24px; } } }
